<template>
  <section :class="fullBox">
    <section
      :class="mainClass"
      :style="{ backgroundImage: ' url(' + bgImage + ')' }"
    >
      <div v-show="false">
        <img :src="backgroundImageMap" alt />
        <img :src="backgroundImage" alt />
      </div>
      <router-view ref="routerview" @ErrorRouter="setOnErrorRouter" />
      <section class="routerChoose">
        <div
          class="routerItem"
          v-for="(item, index) in routerList"
          :key="item.name"
        >
          <div
            class="routerItemName"
            :style="{ backgroundImage: ' url(' + black + ')' }"
          >
            {{ item.name }}
          </div>
          <router-link :to="item.to" class="mainFont">
            <img :src="setTheRouterImage(index + 1)" class="routerItemImage" />
          </router-link>
        </div>
      </section>
    </section>
    <!-- <cenetrBottomView></cenetrBottomView> -->
  </section>
</template>
<script>
/* eslint-disable camelcase */
import black from "@/assets/centerRouter/v1_black.png";
import v1_blue1 from "@/assets/centerRouter/v1_blue1.png";
import v1_blue2 from "@/assets/centerRouter/v1_blue2.png";
import v1_blue3 from "@/assets/centerRouter/v1_blue3.png";
import v1_blue4 from "@/assets/centerRouter/v1_blue4.png";
import v1_blue5 from "@/assets/centerRouter/v1_blue5.png";
import v1_blue6 from "@/assets/centerRouter/v1_blue6.png";
import v1_blue7 from "@/assets/centerRouter/v1_blue7.png";

import v1_gray1 from "@/assets/centerRouter/v1_gray1.png";
import v1_gray2 from "@/assets/centerRouter/v1_gray2.png";
import v1_gray3 from "@/assets/centerRouter/v1_gray3.png";
import v1_gray4 from "@/assets/centerRouter/v1_gray4.png";
import v1_gray5 from "@/assets/centerRouter/v1_gray5.png";
import v1_gray6 from "@/assets/centerRouter/v1_gray6.png";
import v1_gray7 from "@/assets/centerRouter/v1_gray7.png";

import v1_red1 from "@/assets/centerRouter/v1_red1.png";
import v1_red2 from "@/assets/centerRouter/v1_red2.png";
import v1_red3 from "@/assets/centerRouter/v1_red3.png";
import v1_red4 from "@/assets/centerRouter/v1_red4.png";
import v1_red5 from "@/assets/centerRouter/v1_red5.png";
import v1_red6 from "@/assets/centerRouter/v1_red6.png";
import v1_red7 from "@/assets/centerRouter/v1_red7.png";
import backgroundImageMap from "@/assets/DailyOperation/background/2-1-1.jpg";
import backgroundImage from "@/assets/DailyOperation/background/2-1-2.jpg";

import cenetrBottomView from "@/view/DailyOperation/cenetrBottomView";
import websockObject from "@/mixins/websock";
import { mapActions } from "vuex";
const routerList = [
  {
    to: "/dailyMain/map",
    name: "机房线路连通状态"
  },
  {
    to: "/dailyMain/tuopu/东莞",
    name: "东莞凤岗机房网络拓扑图"
  },
  {
    to: "/dailyMain/tuopu/深圳",
    name: "深圳后海机房网络拓扑图"
  },
  {
    to: "/dailyMain/tuopu/上海",
    name: "上海金桥机房网络拓扑图"
  },
  {
    to: "/dailyMain/jifang/东莞",
    name: "东莞凤岗机房俯视图"
  },
  {
    to: "/dailyMain/jifang/深圳",
    name: "深圳后海机房俯视图"
  },
  {
    to: "/dailyMain/jifang/上海",
    name: "上海金桥机房俯视图"
  }
];
export default {
  name: "centerMain",
  data() {
    return {
      black,
      routerList,
      backgroundImageMap,
      backgroundImage,
      v1_blue1,
      v1_blue2,
      v1_blue3,
      v1_blue4,
      v1_blue5,
      v1_blue6,
      v1_blue7,

      v1_gray1,
      v1_gray2,
      v1_gray3,
      v1_gray4,
      v1_gray5,
      v1_gray6,
      v1_gray7,

      v1_red1,
      v1_red2,
      v1_red3,
      v1_red4,
      v1_red5,
      v1_red6,
      v1_red7,

      onErrorRouter: [],
      timeOutObject: null
    };
  },
  mixins: [websockObject],
  components: {
    cenetrBottomView
  },
  computed: {
    bgImage() {
      return this.$route.name === "dailyMainMap" ||
        this.$route.name === "dailyMainJiGui"
        ? this.backgroundImageMap
        : this.backgroundImage;
    },
    mainClass() {
      return this.$route.name === "dailyMainJifang"
        ? "samllMainBox"
        : "mainBox";
    },
    fullBox() {
      return this.$route.name === "dailyMainJifang"
        ? "samllFullBox"
        : "fullBox";
    }
  },
  beforeDestroy() {
    this.timeOutObject && clearTimeout(this.timeOutObject);
  },
  mounted() {
    // eslint-disable-next-line no-undef
    this.wsuri = BaseUrl.dailyCenterMain.ListWebsockUrl;
    // this.getWebsocketMsg([
    //   {
    //     'dateTime': 1599654804826,
    //     'value': [
    //       1,
    //       2,
    //       5
    //     ],
    //     'cabinetId': ' 机柜的uuid',
    //     'group': '机房视图块'
    //   }
    // ])
  },
  methods: {
    ...mapActions(["setTheRouterStatus"]),
    useTheTestDate() {},
    setOnErrorRouter(Router) {
      if (Router.status === 1) {
        let target = this.onErrorRouter.indexOf(Router.index);
        if (target === -1) {
          this.onErrorRouter.push(Router.index);
        } else {
          this.onErrorRouter.splice(target, 1);
        }
      } else {
      }
    },
    updataRouter(sourDate) {
      let routerViewType =
        this.$route.name === "dailyMainMap"
          ? 1
          : this.$route.name === "dailyMainTuopu"
          ? 2
          : 3;
      if (routerViewType === 1) {
        this.$refs.routerview.updateLine(sourDate);
      } else {
        this.$refs.routerview.getWebsocketMsg(sourDate);
      }
    },
    setTheRouterImage(number) {
      let choosed = false;
      switch (number) {
        case 1:
          choosed =
            this.$route.name === "dailyMainMap" ||
            this.$route.name === "dailyMainJiGui";
          break;
        case 2:
          choosed =
            this.$route.name === "dailyMainTuopu" &&
            this.$route.params.type === "东莞";
          break;
        case 3:
          choosed =
            this.$route.name === "dailyMainTuopu" &&
            this.$route.params.type === "深圳";
          break;
        case 4:
          choosed =
            this.$route.name === "dailyMainTuopu" &&
            this.$route.params.type === "上海";
          break;
        case 5:
          choosed =
            this.$route.name === "dailyMainJifang" &&
            this.$route.params.type === "东莞";
          break;
        case 6:
          choosed =
            this.$route.name === "dailyMainJifang" &&
            this.$route.params.type === "深圳";
          break;
        case 7:
          choosed =
            this.$route.name === "dailyMainJifang" &&
            this.$route.params.type === "上海";
          break;
        default:
          choosed = false;
          break;
      }
      let normalBack = choosed ? "v1_blue" + number : "v1_gray" + number;
      let back = this.onErrorRouter.includes(number)
        ? "v1_red" + number
        : normalBack;
      return this.$data[back];
    },
    getWebsocketMsg(sourDate) {
      if (sourDate.length > 0) {
        let _this = this;
        sourDate.map(item => {
          _this.onErrorRouter = item.value;
          _this.setTheRouterStatus(item);
          _this.timeOutObject && clearTimeout(_this.timeOutObject);
          _this.timeOutObject = setTimeout(() => {
            _this.$router.push({
              name: "dailyMainJiGui"
            });
          }, 50000);
        });
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    }
  }
};
</script>

<style scoped>
.allView {
  display: inline-flex;
}
.mainBox {
  width: 5760px;
  height: 2160px;
  position: relative;
}
.routerItemImage {
  max-width: 100%;
}
.samllMainBox {
  width: 1920px;
  height: 720px;
  position: relative;
}
.mainBox .routerChoose {
  width: 530px;
  position: absolute;
  bottom: 55px;
  right: 75px;
}
.mainBox .routerItem {
  position: relative;
  width: 100%;
  height: 265px;
}

.samllMainBox .routerChoose {
  width: 176px;
  position: absolute;
  bottom: 18px;
  right: 25px;
}
.samllMainBox .routerItem {
  position: relative;
  width: 100%;
  height: 88px;
}
.mainBox .routerItemName {
  width: 490px;
  padding: 15px 10px;
  position: absolute;
  bottom: 10px;
  left: 10px;
  z-index: 99;
  color: #fff;
  font-size: 30px;
  display: block;
  line-height: 1;
}
.samllMainBox .routerItemName {
  width: 100%;
  padding: 5px 3px;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 99;
  color: #fff;
  font-size: 10px;
  display: block;
  line-height: 1;
}
</style>



