<template>
  <section class="centerView" :style="{ width: 1920 * 3 + 'px' }">
    <leftHeader></leftHeader>
    <leftCenter></leftCenter>
    <leftBottom></leftBottom>
  </section>
</template>

<script>
/**
 * 页面说明
 * @路由: /left
 * @用途：左面三板
 */
import leftHeader from "@/view/DailyOperation/centerView/header";
import leftCenter from "@/view/DailyOperation/centerView/center";
import leftBottom from "@/view/DailyOperation/centerView/bottom";
export default {
  name: "centerView",
  components: {
    leftHeader,
    leftCenter,
    leftBottom
  }
};
</script>

<style scoped>
.centerView {
  display: flex;
  transform: translate3d(0, 0, 0);
}
</style>



